import React, { useState } from "react";
import "./Slider.scss";
import NavigateNextIcon from "@mui/icons-material/NavigateNext";
import NavigateBeforeIcon from "@mui/icons-material/NavigateBefore";

export default function Slider() {
  const [index, setIndex] = useState(0)
  const data = [
    "https://images.pexels.com/photos/1549200/pexels-photo-1549200.jpeg?auto=compress&cs=tinysrgb&w=1600",
    "https://images.pexels.com/photos/949670/pexels-photo-949670.jpeg?auto=compress&cs=tinysrgb&w=1600",
    "https://images.pexels.com/photos/837140/pexels-photo-837140.jpeg?auto=compress&cs=tinysrgb&w=1600",
  ];




  const handleLeft = () => {
    setIndex(index === 0?2:(prev)=>prev-1)
  };


  const handleRight = () => {
    setIndex(index === 2?0:(prev)=>prev+1)
  
  };
  return (
    <>
      <div className="slider">
        <div className="images" style={{transform:`translateX(-${index * 100}vw)`}}>
          <img src={data[0]} alt="" />
          <img src={data[1]} alt="" />
          <img src={data[2]} alt="" />
        </div>
        <div className="icons">
          <div onClick={handleLeft} id="left" className="icon left">
            <NavigateBeforeIcon />
          </div>

          <div  onClick={handleRight} id="right" className="icon right">
            <NavigateNextIcon />
          </div>
        </div>
      </div>
    </>
  );
}
